.Headerbar-Container {
  --top-offset: -25px;
  --max-height-headerbar: 90px;
  --background-color-headerbar: #1e1e1ef2;
  --borderstyle-headerbar: 2px solid white;
}

.Headerbar {
  background-color: var(--background-color-headerbar);
  border-bottom: var(--borderstyle-headerbar);
  top: var(--top-offset);
  width: 100vw;
  z-index: 999;
  max-height: var(--max-height-headerbar);
  grid-template-columns: 100vw;
  grid-auto-rows: 70px;
  justify-items: center;
  padding-top: 20px;
  transition: max-height .5s ease-in-out;
  display: grid;
  position: fixed;
  overflow: hidden;
  box-shadow: 0 0 10px 15px #00000080;
}

.Headerbar::-webkit-scrollbar {
  width: 0;
}

.Headerbar__Link {
  color: #ccc;
  text-decoration: none;
}

.Headerbar__Filler {
  display: none;
}

.Headerbar__Button {
  cursor: pointer;
  width: 90%;
  border-bottom: 1px solid #ffffff3f;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  transition: transform .1s;
  display: flex;
}

.Headerbar .Headerbar__Button:last-child {
  border: 0;
}

.Headerbar__Button:hover {
  z-index: 100;
  transform: scale(1.01);
}

.Headerbar__Label {
  text-transform: uppercase;
  text-align: left;
  user-select: none;
  font-size: 3rem;
}

.Headerbar__Logo-And-Menu {
  width: 100%;
  border-bottom: 1px solid #fff;
  flex-direction: row;
  order: -1;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.Headerbar__Logo {
  justify-content: space-between;
  align-items: center;
  margin: 10px;
  display: flex;
}

.Logo__Text {
  user-select: none;
  color: var(--header-color);
  margin: 0;
  font-size: 2.5rem;
  font-weight: 600;
  text-decoration-color: #fff;
}

.Button__Menu {
  width: fit-content;
  border: 0;
}

@media screen and (min-width: 600px) {
  .Headerbar__Logo {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
  }

  .Button__Menu {
    position: absolute;
    right: 0;
  }
}

.Headerbar--ausgeklapt {
  max-height: calc(100vh - var(--top-offset) );
