}

@media screen and (min-width: 1250px) {
  .Headerbar {
    --max-height-headerbar: 150px;
    flex-direction: row;
    align-items: flex-end;
    padding-right: 10px;
    display: flex;
    top: -55px;
  }

  .Headerbar__Label {
    font-size: 1.5rem;
    font-weight: 500;
  }

  .Headerbar__Filler {
    display: block;
  }

  .Headerbar__Button {
    width: fit-content;
    border: 0;
    flex-direction: column;
    position: relative;
    bottom: 10px;
  }

  .Headerbar__Button:hover {
    z-index: 100;
    transform: scale(1.1);
  }

  .Headerbar__Icon {
    transform: scale(.6)translateY(25px);
  }

  .Headerbar__Button:not(.Button__Home):not(.Button__Products) .Headerbar__Icon {
    transform: scale(.45)translateY(60px);
  }

  .Headerbar__Logo-And-Menu {
    width: fit-content;
    border: 0;
    order: 0;
  }

  .Headerbar__Logo-And-Menu .Button__Menu {
    display: none;
  }

  .Headerbar__Logo {
    flex-direction: column;
    align-items: center;
    transform: translateY(-55px)translateX(-50%);
  }

  .BM_Logo {
    transform: translateY(14px);
  }

  .Logo__Text {
    font-size: 2rem;
  }
}

