.button_left_arrow {
  height: var(--arrow-size);
  width: var(--arrow-size);
  border-left: var(--arrow);
  border-top: var(--arrow);
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-40%, -50%)rotate(-45deg);
}

.button_right {
