.button_right_arrow {
  height: var(--arrow-size);
  width: var(--arrow-size);
  border-right: var(--arrow);
  border-top: var(--arrow);
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-60%, -50%)rotate(45deg);
}

@keyframes arrow-pulsing-right {
  50% {
    left: 60%;
