  --bg-transparent: #fff0;
  --bg-normal: #ffffff1a;
  background-color: #ffffff1a;
  border: 1px solid #a5a5a5;
  border-radius: 100%;
  margin: 0 2px;
  display: inline-block;
  box-shadow: inset 0 -2px 3px -1px #000;
}

@keyframes image_dot_initial_blink {
  0% {
    background-color: var(--bg-transparent);
  }

  33% {
    background-color: var(--bg-normal);
  }

  34% {
    background-color: var(--bg-transparent);
  }

  66% {
    background-color: var(--bg-normal);
  }

  67% {
    background-color: var(--bg-transparent);
  }

  100% {
    background-color: var(--bg-normal);
  }
}

.image_dot:not(.image_dot_highlighted):hover {
  background-color: #ffffffbf;
}

.image_dot_initial_animation {
  animation-name: image_dot_initial_blink;
  animation-duration: .9s;
  animation-iteration-count: 1;
  animation-delay: 2s;
  animation-fill-mode: forwards;
}

@keyframes dot-pulsing {
  0% {
    background-color: #fff;
  }

  50% {
    background-color: #797979;
  }

  100% {
    background-color: #fff;
  }
}

.image_dot_highlighted {
  animation-name: dot-pulsing;
  animation-duration: 2s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}

.Produkt-Block--Sky, .Produkt-Block--Mountain, .Produkt-Block--Spezial, .Produkt-Block--Aqua {
  --gradient-orientation: -10deg;
}

.Footer {
  width: 100%;
  z-index: 4;
  background-color: #191919;
  border-top: 3px solid #fff;
  flex-direction: column;
  justify-content: center;
  padding: 20px 10px 60px;
  display: flex;
  position: relative;
}

.Footer__Section {
  width: 90%;
  max-width: 600px;
  border-bottom: 1px solid #ffffff4d;
  flex-direction: column;
  align-self: center;
  padding: 10px 0;
  display: flex;
}

.Footer__Liste {
  max-height: 0;
  opacity: 0;
  margin: 0;
  padding: 0;
  transition: all .5s;
  overflow: hidden;
}

.Footer--ausgeklappt .Footer__Liste {
  max-height: 100vh;
  opacity: 1;
}

.Footer__Section__Header {
  margin: 0;
}

.Footer__Menu-Header {
  cursor: pointer;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.Footer__Listen-Element {
  color: #7e7e7e;
  width: fit-content;
  cursor: pointer;
  margin-bottom: .7rem;
  list-style: none;
}

.Footer__Listen-Element:hover {
  color: #dbdbdb;
}

.Footer__Logo {
  flex-direction: column;
  order: 100;
  align-items: center;
  margin: 0 50px;
  display: flex;
}

.Footer__Logo__Header {
  margin: 0;
}

.Footer__Logo__Text {
  color: #a0a0a0;
}

.Footer__Menu-Button {
  width: 40px;
  height: 40px;
  cursor: pointer;
  user-select: none;
  background-color: #414141;
  border-radius: 100%;
  transition: transform .4s;
  transform: rotateZ(45deg);
}

.Footer--ausgeklappt .Footer__Menu-Button {
  transform: rotateZ(-135deg);
}

.Menu-Button__Arrow {
  --arrow-style: 3px solid white;
  border-right: var(--arrow-style);
  border-bottom: var(--arrow-style);
  --width-and-height: 40%;
  width: var(--width-and-height);
  height: var(--width-and-height);
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-55%, -55%);
}

@media screen {
  .Footer {
    font-size: 2rem;
  }
}

@media screen and (min-width: 1440px) {
  .Footer {
    flex-direction: row;
    justify-content: space-around;
  }

  .Footer__Section {
    border-bottom: 0;
    align-self: flex-start;
    margin-right: 20px;
  }

  .Footer__Liste {
    max-height: 100%;
    opacity: 1;
    margin-top: 1rem;
  }

  .Footer__Menu-Button {
    display: none;
  }

  .Footer__Menu-Header {
    cursor: default;
  }

  .Footer__Logo {
    order: 0;
  }
}



